<template>
    <div class="container">
        <h2 class="page-header">Install</h2>
        <p>use <strong>npm</strong> to install plugin</p>
        <pre class="brush:bash">
            npm i v-region --save
        </pre>
        <p>Include plugin in your <code>main.js</code> file</p>
        <pre class="brush:js">
            import Vue from 'vue'
            import vRegion from 'v-region';

            ...

            Vue.use(vRegion);
        </pre>

        <h2 class="page-header">Deploy on your component</h2>
        <p>template code</p>
        <script type="syntaxhighlighter" class="brush:js">
        <![CDATA[
            <template>
                <v-region @values="regionChange" class="form-control"></v-region>
            </template>
        ]]>
        </script>

        <p>script code</p>
        <pre class="brush:js">
        export default {
            methods:{
                //receive selected region data
                regionChange(data){
                    console.log(data);
                }
            }
        };
        </pre>

        <h2 class="page-header">Example <small>base mode and ui selector mode</small></h2>

        <h4>Province</h4>
        <v-region :city="false" :area="false" class="form-control"></v-region><br>


        <h4>Province、City</h4>
        <v-region :area="false" class="form-control"></v-region><br>

        <h4>Province、City、Area/County</h4>
        <v-region class="form-control"></v-region><br>

        <h4>Province、City、Area/County、Town/Street</h4>
        <p>region selected result: </p>
        <pre style="border: 0;">{{JSON.stringify(selectResult, null, 4)}}</pre>
        <v-region :town="true" @values="selectLogs" class="form-control"></v-region><br>

        <h4>init region selected</h4>
        <v-region :town="true" :selected="selected" class="form-control"></v-region><br>

        <h4>region dropdown selector <small><a href="https://codepen.io/terry05/pen/ERNvzJ" target="_blank">view on CodePen</a></small></h4>
        <v-region :town="true" :ui="true" @values="regionChange" class="form-control">
            <button type="button" class="btn btn-default">
                {{btnText}} <i class="fa fa-fw fa-caret-down"></i>
            </button>
        </v-region>



        <h2 class="page-header">Options</h2>
        <table class="table table-bordered table-striped">
            <thead>
                <tr>
                    <th>name</th>
                    <th>type</th>
                    <th>description</th>
                    <th>required</th>
                    <th>default</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>blank</td>
                    <td>Boolean</td>
                    <td>build default 'please select' item, the value was blank</td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>city</td>
                    <td>Boolean</td>
                    <td>enable city level</td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>area</td>
                    <td>Boolean</td>
                    <td>enable area/county level</td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>town</td>
                    <td>Boolean</td>
                    <td>enable town/street</td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>ui</td>
                    <td>Boolean</td>
                    <td>enable ui selector mode</td>
                    <td>false</td>
                    <td>false</td>
                </tr>
                <tr>
                    <td>search</td>
                    <td>Boolean</td>
                    <td>enable ui selector search bar</td>
                    <td>false</td>
                    <td>true</td>
                </tr>
                <tr>
                    <td>selected</td>
                    <td>Object</td>
                    <td>init region selected item<br>
                        <ul>
                            <li><code>province</code> province code</li>
                            <li><code>city</code> city code</li>
                            <li><code>area</code> area code</li>
                            <li><code>town</code> town code</li>
                        </ul>
                        example: <br>
                        <code>{province:'350000',city:'350100',area:'350103',town:'350103012'}</code>
                    </td>
                    <td>false</td>
                    <td></td>
                </tr>
            </tbody>
        </table>

        <h2 class="page-header">Events</h2>
        <table class="table table-bordered table-striped">
            <thead>
            <tr>
                <th>name</th>
                <th>description</th>
                <th>optionalPayload</th>
            </tr>
            </thead>
            <tbody>
            <tr>
                <td>values</td>
                <td>return selected region data</td>
                <td>
                    <pre class="brush:js">
                        {
                            province:{key:'',value:''},
                            city:{key:'',value:''},
                            area:{key:'',value:''},
                            town:{key:'',value:''}
                        }
                    </pre>
                </td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    export default {
        name: "Demo",
        data(){
            return {
                selected: {
                    province: '350000',
                    city: '350100',
                    area: '350103',
                    town: '350103012'
                },
                btnText: 'please select',
                selectResult: {}
            };
        },
        methods: {
            regionChange(d){
                console.log(d);
                this.btnText = (!d.province&&!d.city&&!d.area&&!d.town)?'请选择':
                    `${this.getValue(d.province)}${this.getValue(d.city)}${this.getValue(d.area)}${this.getValue(d.town)}`.trim();
            },
            getValue(obj){
                return obj?obj.value:'';
            },
            selectLogs(data){
                if(data) this.selectResult = data;
            }
        },
        mounted(){
            SyntaxHighlighter.highlight();
        }
    }
</script>

<style lang="scss" scoped>

</style>


